import React, { Component } from 'react'
import axios from 'axios';
import {Link} from "react-router-dom"

export default class Login extends Component {
    user_data = {
        email: "",
        password: ""
    }

    getFormData = ({ target }) => {
        this.user_data[target.name] = target.value
    }

    sendData = async (e) => {
        e.preventDefault();
        const { data } = await axios.post('https://route-egypt-api.herokuapp.com/signin', this.user_data);
        if (data.message === "success") {

            localStorage.setItem('token', data.token);

            {this.props.history.replace('/home')}

        } else if (data.message === "email doesn't exist") {
            document.querySelector('.mail').classList.remove('d-none');
            document.querySelector('.pass').classList.add('d-none')
        } else {
            document.querySelector('.pass').classList.remove('d-none');
            document.querySelector('.mail').classList.add('d-none');
        }
    }

    render() {
        return (
            <>
                <div className="w-75 mx-auto py-5 d-flex flex-column justify-content-center login">
                    <form onSubmit={this.sendData}>
                        <input onChange={this.getFormData} name="email" type="email" className="form-control" />
                        <div className="alert alert-warning mail d-none" role="alert">
                            email doesn't exist
                        </div>
                        <input onChange={this.getFormData} name="password" type="password" className="form-control mt-3" />
                        <div className="alert alert-warning pass d-none" role="alert">
                            incorrect password
                        </div>
                        <button className="btn btn-primary w-100 mt-3">Login</button>
                        <p className="text-muted my-2 text-center">
                            don't have an account? <Link to="/signUp">Sign up</Link>
                        </p>
                    </form>
                </div>
            </>
        )
    }
}
